.heading h1 {
  font-family: "Dancing Script", cursive;
  position: relative;
  width: max-content;
  font-size: 48px;
  margin: auto;
}
.heading h1:before {
  content: "";
  position: absolute;
  width: 90%;
  height: 3px;
  left: 0;
  right: 0;
  bottom: -5px;
  margin: 0 auto;
  background: #ffc107;
  border-radius: 5px;
}

.menu-img {
  flex: none;
}
.menu-img img {
  width: 300px;
  height: 200px;
  border: 4px solid #ffc107;
  border-radius: 5px;
}
.menu-wrapper {
  display: flex;
  align-items: flex-start;
}

.menu-wrapper .top-sec h5 {
  text-transform: capitalize;
  font-family: "Dancing Script", cursive;
  font-weight: 700;
  font-size: 26px;
}
.menu-wrapper .top-sec {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px dotted #ff9800;
  margin-bottom: 15px;
  padding-bottom: 12px;
}
.menu-wrapper .top-sec .price {
  color: #ec8d00;
  font-weight: 600;
  background: #ff980040;
  padding: 8px 15px;
  border-radius: 10px;
}
.menu-desc {
  margin-left: 15px;
}
.category-list {
  list-style: none;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  justify-content: center;
  margin-bottom: 70px;
}
.category-list li {
  padding: 6px 15px;
  border-radius: 5px;
  cursor: pointer;
  background: transparent;
  color: #ec8d00;
  font-weight: 400;
  margin-right: 10px;
  border: 2px solid transparent;
  transition: 0.3s;
  text-transform: capitalize;
  border: 1px solid #ec8d00;
}
.category-list li:hover {
  background-color: #ec8d00;
  border-color: #ec8d00;
  color: #fff;
}
.activeTab {
  background-color: #ec8d00 !important;
  border-color: #ec8d00 !important;
  color: #fff !important;
}
@media (max-width: 595.98px) {
  .menu-wrapper {
    display: block;
  }
  .menu-desc {
    margin-left: 0;
    margin-top: 20px;
  }
  .menu-img img {
    width: 100%;
  }
}
